<script>
  import { select, selectAll } from "d3-selection";
  import { hierarchy, cluster } from "d3-hierarchy";
  import { scaleLinear, scaleSqrt } from "d3-scale";
  import { descending } from "d3-array";
  import { beforeUpdate, afterUpdate, tick } from "svelte";
  import { quintOut } from "svelte/easing";
  import { fade, draw } from "svelte/transition";
  import tippy from "sveltejs-tippy";
  import TripNode from "./TripNode.svelte";

  export let allData;
  export let selectedYear;
  export let selectedIndicator;
  export let colorBy;
  export let observed;

  const padding = { top: 20, right: 20, bottom: 20, left: 20 };

  $: width = 300;
  $: height = 280;

  $: w = 300;
  $: svgWidth = 400;

  $: yScale = scaleLinear()
    .domain([160, 0])
    .range([height - padding.bottom, padding.top]);

  $: bubbleScale = scaleSqrt()
    .domain([
      1,
      Math.max(
        ...allData.tripsData.map((d) =>
          Math.max(...d.children.map((c) => c.total))
        )
      ),
    ])
    .range([5, 18]);

  let yearData;
  let rootArray = [];

  beforeUpdate(() => {
    if (observed) {
      drawChart(selectedYear, selectedIndicator);
    }
  });

  afterUpdate(() => {
    updateScreen();
  });

  async function updateScreen() {
    await tick();
    updateLateralNodes();
  }

  const drawChart = (year, indicator) => {
    yearData = allData.tripsData.map((d) => {
      return {
        name: "root",
        children: [
          ...d["children"]
            .filter((c) => c.year === year)
            .sort((a, b) =>
              a[indicator].toString().localeCompare(b[indicator].toString())
            ),
        ],
      };
    });

    rootArray = yearData.map((d) => {
      return d3Cluster(hierarchy(d));
    });
  };

  const formatTooltip = (data, selector) => {
    const template = `
      <p><span>Countr${data.data.total > 1 ? "ies" : "y"}:</span> ${
      data.data.country.length > 0 ? data.data.country : "N/A (Virtual)"
    }</p>
      <p><span>Itenerary:</span> ${data.data.description_english}</p>
    `;

    return {
      content: template,
      allowHTML: true,
      placement: "top",
      triggerTarget: document.getElementById(selector),
    };
  };

  $: svgSize = svgWidth > 440 ? 110 : parseInt(svgWidth / 4);

  // First arg of size is the total angle of the dendro
  // Second arg is the overall scale
  $: d3Cluster = cluster().size([180, svgSize]);

  // Returns x, y values that format the dendrogram radially
  function project(x, y) {
    // Value subtracted by x is the rotation of the dendro
    const angle = ((x - 180) / 180) * Math.PI;
    const radius = y;
    return [radius * Math.cos(angle), yScale(radius * Math.sin(angle))];
  }

  const drawPath = (d, isActive, reverse) => {
    const dist = isActive === "isActive" ? 55 : 30;

    if (reverse) {
      return (
        "M" +
        project(d.parent.x, d.parent.y) +
        "C" +
        project(d.parent.x, (d.y + d.parent.y) / 2) +
        " " +
        project(d.x, (d.y + d.parent.y + 50) / 2) +
        " " +
        project(d.x, d.y + dist)
      );
    } else {
      return (
        "M" +
        project(d.x, d.y + dist) +
        "C" +
        project(d.x, (d.y + d.parent.y + 50) / 2) +
        " " +
        project(d.parent.x, (d.y + d.parent.y) / 2) +
        " " +
        project(d.parent.x, d.parent.y)
      );
    }
  };

  // Focuses (extend) trip path and circle on hover
  const focusPath = (id, bool) => {
    select(`#extend-${id}`).classed("isHidden", !bool);
    select(`#extend-circle-${id}`).classed("isHidden", !bool);
    select(`#path-${id}`).classed("isHidden", bool);
    select(`#circle-${id}`).classed("isHidden", bool);
    select(`#cover-${id}`).classed("isHidden", bool);

    updateLateralNodes();
  };

  const orderBubbles = () => {
    rootArray.forEach((rootObj) => {
      let allCircles = selectAll(
        `[data-id="${rootObj.data.children[0].top_leaders.replace(
          " ",
          "-"
        )}"] .bubble`
      );
      allCircles.data(rootObj.children).sort((a, b) => {
        return descending(a.data.total, b.data.total);
      });
    });
  };

  const setNodePositions = (path, node, dist) => {
    let x = path.getPointAtLength(dist).x;
    let y = path.getPointAtLength(dist).y;

    node.setAttribute("transform", `translate(${x}, ${y})`);
  };

  const updateLateralNodes = () => {
    rootArray.forEach((rootObj, leaderIndex) => {
      if (!rootObj.data.children[0]) return;

      let allPaths = selectAll(
        `[data-id="${rootObj.data.children[0].top_leaders.replace(
          " ",
          "-"
        )}"] .trip-path`
      );
      allPaths.data(rootObj.children).each((a, tripIndex) => {
        let path = document.getElementById(
          `tooltip-path-${leaderIndex}-${tripIndex}`
        );

        // Update position of lateral nodes of currently hovered trip
        let tripPath = document.getElementById(
          `path-${leaderIndex}-${tripIndex}`
        );
        if (tripPath.classList.contains("isHidden")) {
          path = document.getElementById(`extend-${leaderIndex}-${tripIndex}`);
        }

        let biNode = document.getElementById(
          `biNode-${leaderIndex}-${tripIndex}`
        );

        let multiNode = document.getElementById(
          `multiNode-${leaderIndex}-${tripIndex}`
        );

        let tippyRef = document.getElementById(
          `tippyRef-${leaderIndex}-${tripIndex}`
        );

        setNodePositions(path, biNode, 30);
        setNodePositions(path, multiNode, 40);
        setNodePositions(path, tippyRef, 60);
      });
    });
  };

  // values for animation in leader order
  const delays = {
    "Xi Jinping": 800,
    "Li Keqiang": 1600,
    "Yang Jiechi": 2400,
    "Wang Yi": 3200,
    Other: 4000,
  };
</script>

{#key rootArray}
  <div class="interatcive__charts charts__container" bind:clientWidth={w}>
    {#each rootArray as leader, leaderIndex}
      <figure
        bind:clientWidth={svgWidth}
        class=" leader leader-{leader.children
          ? leader.children[0].data.top_leaders.replace(' ', '-')
          : ''}"
        data-id={leader.children
          ? leader.children[0].data.top_leaders.replace(" ", "-")
          : ""}
      >
        <svg {width} {height}>
          <defs>
            {#each leader.descendants().slice(1) as trip, i}
              <marker
                id="marker-{leaderIndex}-{i}"
                refX={bubbleScale(trip.data.total)}
                refY={bubbleScale(trip.data.total)}
                markerWidth={bubbleScale(trip.data.total) * 2}
                markerHeight={bubbleScale(trip.data.total) * 2}
                orient="auto"
              >
                <circle
                  class="node-marker"
                  cx={bubbleScale(trip.data.total)}
                  cy={bubbleScale(trip.data.total)}
                  r={bubbleScale(trip.data.total)}
                  fill="#fff"
                />
              </marker>
            {/each}
          </defs>
          <g class="g1" transform="translate({svgWidth / 2}, 235)">
            {#each leader.descendants().slice(1) as trip, tripIndex}
              <path
                id={`extend-${leaderIndex}-${tripIndex}`}
                class="extend-path isHidden"
                d={drawPath(trip, "isActive", false)}
                fill="none"
                stroke="#303f46"
                data-virtual={trip.data.region}
                marker-start="url(#marker-{leaderIndex}-{tripIndex})"
              />

              <path
                id={`path-${leaderIndex}-${tripIndex}`}
                class="trip-path"
                d={drawPath(trip, false, true)}
                fill="none"
                stroke="#303f46"
                data-virtual={trip.data.region}
                marker-end="url(#marker-{leaderIndex}-{tripIndex})"
                in:draw={{
                  duration: 1000,
                  delay: delays[leader.children[0].data.top_leaders],
                  easing: quintOut,
                }}
              />

              <path
                id={`tooltip-path-${leaderIndex}-${tripIndex}`}
                class="tooltip-path"
                d={drawPath(trip, false, false)}
                fill="none"
                stroke="transparent"
                stroke-width="8"
                on:mouseenter={() =>
                  focusPath(`${leaderIndex}-${tripIndex}`, true)}
                on:mouseleave={() =>
                  focusPath(`${leaderIndex}-${tripIndex}`, false)}
              />

              <defs>
                <pattern id="striped-bubble" width="10%" height="10%">
                  <circle id="striped-bubble" r="2" fill="#000" />
                </pattern>
              </defs>

              <TripNode
                uniqueIndex={`${leaderIndex}-${tripIndex}`}
                name="circle"
                r={bubbleScale(trip.data.total)}
                translateVal={project(trip.x, trip.y + 30)}
                {trip}
                {colorBy}
                leader={leader.children[0].data.top_leaders}
                animation={false}
              />

              <TripNode
                uniqueIndex={`${leaderIndex}-${tripIndex}`}
                name="extend-circle"
                className="isHidden"
                r={bubbleScale(trip.data.total)}
                translateVal={project(trip.x, trip.y + 55)}
                {trip}
                {colorBy}
                leader={leader.children[0].data.top_leaders}
              />

              <circle
                id={`extendTippyRef-${leaderIndex}-${tripIndex}`}
                class="tippy-ref"
                cx="0"
                cy="0"
                r={bubbleScale(trip.data.total)}
                fill="red"
                transform="translate({project(trip.x, trip.y + 55)})"
                use:tippy={formatTooltip(
                  trip,
                  `tooltip-node-${leaderIndex}-${tripIndex}`
                )}
              />

              <TripNode
                uniqueIndex={`${leaderIndex}-${tripIndex}`}
                className="tooltip-node"
                name="tooltip-node"
                r={bubbleScale(trip.data.total) + 5}
                translateVal={project(trip.x, trip.y + 30)}
                {trip}
                {focusPath}
                leader={leader.children[0].data.top_leaders}
              />

              <circle
                id={`biNode-${leaderIndex}-${tripIndex}`}
                class="bilateral-node"
                cx="0"
                cy="0"
                r="3"
                fill="#303f46"
                in:fade={{
                  duration: 1500,
                  delay:
                    delays[leader.children[0].data.top_leaders] +
                    200 * leaderIndex,
                  easing: quintOut,
                }}
              />

              <circle
                id={`multiNode-${leaderIndex}-${tripIndex}`}
                class="multilateral-node"
                class:hidden={!trip.data.multilateral}
                cx="0"
                cy="0"
                r="3"
                fill="#303f46"
                in:fade={{
                  duration: 1500,
                  delay: delays[leader.children[0].data.top_leaders],
                  easing: quintOut,
                }}
              />

              <circle
                id={`tippyRef-${leaderIndex}-${tripIndex}`}
                class="tippy-ref"
                cx="0"
                cy="0"
                r="0"
                fill="transparent"
                use:tippy={formatTooltip(
                  trip,
                  `tooltip-path-${leaderIndex}-${tripIndex}`
                )}
              />
            {/each}
            <text
              in:fade={{
                duration: 1500,
                delay: delays[leader.children[0].data.top_leaders],
                easing: quintOut,
              }}
              x="0"
              y="40"
              >{leader.children
                ? leader.children[0].data.top_leaders
                : ""}</text
            >
          </g>
        </svg>
      </figure>
    {/each}

    <div
      class="legend"
      style="max-height: {height}px; max-width: {svgWidth > 400
        ? 400
        : svgWidth}px;"
    >
      <div
        class="img-wrapper"
        in:fade={{ duration: 1500, delay: 4800, easing: quintOut }}
      >
        <img
          src={selectedIndicator === "region"
            ? "./images/Legend400x2.svg"
            : "./images/Legend400x2Income.svg"}
          alt="Legend"
          title="Legend"
        />
      </div>
    </div>
  </div>
{/key}

<style type="text/scss" global>
  // Transform necessary for placement - following css will move to sass files

  text {
    text-anchor: middle;
    text-transform: uppercase;

    fill: #303f46;
  }
  figure {
    width: 100%;
    margin: 1rem 0;
  }
  svg {
    width: 100%;
  }

  input {
    display: block;
    width: 500px;
    max-width: 100%;
  }

  @import "../scss/custom/_chart.scss";
  @import "../scss/layout/_layout.scss";
  @import "../scss/components/_tooltips.scss";
</style>
